.c_design {
    border-radius: 5px;
    background: #e0e0e0;
    box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
    min-height: 40rem;
    margin-bottom: 2rem;
    margin-top: 2rem;
}

.c_design>img {
    min-height: 17rem;
}

.c_design>div>a {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 2rem;
}